.welcomeSpace {
  min-height: 100vh;
  position: relative;
  display: flex;
}

.welcomeSpace p { 
  font-family: Raleway; 
  font-size: 100px; 
  font-style: normal; 
  -webkit-font-feature-settings: "c2sc"; 
          font-feature-settings: "c2sc"; 
  font-variant: small-caps; 
  font-weight: 400; 
  line-height: 49.5px; 
  margin: auto;
  max-width: 100%;
  color: white;
}

.rentalWelcomeSpace {
  min-height: 50vh;
  position: relative;
  display: flex;
}

.rentalWelcomeSpace p { 
  font-family: Raleway; 
  font-size: 100px; 
  font-style: normal; 
  -webkit-font-feature-settings: "c2sc"; 
          font-feature-settings: "c2sc"; 
  font-variant: small-caps; 
  font-weight: 400; 
  line-height: 49.5px; 
  margin: auto;
  max-width: 100%;
  color: white;
}

.contents {
  padding-top: 10px;
  position: relative;
}

.main-footer {
  min-height: 70px;
  max-height: 70px;
  min-width: 100%;
  background-color: #f8f9fa!important;
}

.blockquote-footer {
  margin-top: 10px;
}

.grayTop {
  background-color: rgba(0,0,0,.03);
  min-width: 100%;
  min-height: 20px;
  max-height: 20px;
}

.middleText {
  text-align: center;
  margin-top: 10px;
}

.blogImg {
  max-width: 100%;
  max-height: 100%;
}

.noPadding {
  padding: 0px;
}

.noPadding footer {
  margin-bottom: 10px;
}

.marginBottom {
  margin-bottom: 5px;
}

.anchor{
  display: block;
  position: relative;
  top: -70px;
  visibility: hidden;
}

.inputWidth {
  min-width: 98%;
  max-width: 98%;
}

.headerRight {
  background-color: rgba(0,0,0,.12);
  text-align: right;
}

@media (max-width: 992px) {
  .smallScreen {
    padding-top: 25px;
  }
}

@media (min-width: 992px) {
  .bigScreen {
    padding-right: 0px;
  }
}

.smallPadding {
  padding: 5px;
}

.minusPadding {
  padding-left: 15px;
  padding-right: 15px;
}

.smallMargin {
  margin-right: 10px;
}

.smallMarginDouble {
  margin-right: 5px;
  margin-left: 5px;
}

.twenyBottom {
  padding-bottom: 20px;
}
